.currency-bar {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin-left: 0.5em;
}
.country {
  color: #3f3f3f;
}
.country:hover {
  color: #6e6d6d;
  cursor: pointer;
}
.country {
  display: flex;
  justify-content: space-between;
  width: 13.8em;
  margin-right: 13%;
  box-shadow: none;
  padding: 8px 16px 8px 10px;
  font-size: var(--font-size-body-smallest);
  border: none;
  background-color: #ffffff;
}

/* down arrow */
.arrow {
  border: solid black;
  border-width: 0 0.1em 0.1em 0;
  display: inline-block;
  padding: 0.15em;
}
.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
.country span {
  display: flex;
  padding-top: 0.3em;
  padding: none;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 100px;
  background-color: #f5f5f5;
  -webkit-border-radius: 100px;
  -moz-border-radius: 100px;
  -ms-border-radius: 100px;
  -o-border-radius: 100px;
}

/* dropdown content */
.country-wrapper {
  position: absolute;
  right: 12.7%;
  top: 2.3em;
  width: 13.8em;
  overflow: hidden;
  height: 18.8em;
  border-radius: 0.2em;
  -webkit-border-radius: 0.2em;
  -moz-border-radius: 0.2em;
  -ms-border-radius: 0.2em;
  -o-border-radius: 0.2em;
}
.dropdown-content {
  z-index: 99;
  position: absolute;
  background: #fff;
  overflow-y: scroll;
  padding: 0.4em 0;
  width: 13.8em;
  height: 18em;
  color: #333;
}
.dropdown-content li {
  list-style: none;
  padding: 8px 45px 8px 12px;
  font-size: var(--font-size-body-smaller);
  text-align: left;
}
.dropdown-content li:hover {
  cursor: pointer;
  background-color: rgb(244, 242, 242);
}
.dropdown-content ul {
  padding: 0;
  margin: 0;
}

/* currency bar 2 */
.country-wrapper2 {
  position: absolute;
  top: -560%;
  left: 11%;
  height: 20em;
  border: 0.5px solid #d2d2d2;
  border-bottom: none;
}

@keyframes up {
  100% {
    transform: scaleY(0);
    -webkit-transform: scaleY(0);
    -moz-transform: scaleY(0);
    -ms-transform: scaleY(0);
    -o-transform: scaleY(0);
  }
  0% {
    transform: scale(1);
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
  }
}
