/* size */
.carousel-two-wrapper {
  width: 100%;
  height: 24em;
  display: flex;
  justify-content: center;
  overflow: hidden;
  padding-bottom: 10em;
}
.carousel-two-wrapper .carousel-inner-wrap {
  height: 24em;
}

.carousel-two-wrapper .outer {
  z-index: 1;
}

.dis-two div:nth-of-type(2) {
  margin-top: 0.1em;
}
.carousel-two-wrapper .c2 {
  height: 100%;
}
.carousel-two-wrapper .c1 {
  height: 100%;
}
.carousel-two-wrapper .c1-overlay {
  height: 100%;
}

/* hover display buttons */
.carousel-two-wrapper .drop1,
.drop3 {
  display: none;
}
.carousel-two-wrapper .c2:hover .drop3 {
  position: absolute;
  display: block;
}
.carousel-two-wrapper .drop1,
.drop2 {
  display: none;
}
.carousel-two-wrapper .c2:hover .drop2 {
  position: absolute;
  display: block;
}
.carousel-two-wrapper .nones {
  display: flex !important;
}
.carousel-two-wrapper .t2 {
  display: flex;
  margin-left: 1.35em;
  margin-top: -0.7em;
}
.carousel-two-wrapper .d2:hover {
  height: 124%;
  transition: height 0.1s ease-in-out;
  -webkit-transition: height 0.1s ease-in-out;
  -moz-transition: height 0.1s ease-in-out;
  -ms-transition: height 0.1s ease-in-out;
  -o-transition: height 0.1s ease-in-out;
}
.carousel-two-wrapper .d1:hover {
  height: 111%;
  transition: height 0.1s ease-in-out;
  -webkit-transition: height 0.1s ease-in-out;
  -moz-transition: height 0.1s ease-in-out;
  -ms-transition: height 0.1s ease-in-out;
  -o-transition: height 0.1s ease-in-out;
}
.carousel-two-wrapper .d2:hover .drop3 {
  top: 76.7%;
  left: 2.2%;
}
.carousel-two-wrapper .d1:hover .drop2 {
  top: 87%;
  left: 5.2%;
}

/* arrows */
.carousel-two-wrapper .Larrow {
  margin-top: 5em;
}
.carousel-two-wrapper .left-button-opacity {
  margin-top: 5em;
}
.carousel-two-wrapper .Rarrow {
  margin-bottom: -6.5em;
}
.carousel-two-wrapper .right-button-opacity {
  margin-bottom: -6.5em;
}

/* images */
.carousel-two-wrapper .dis-none {
  display: none;
}

.carousel-two-wrapper .dis-two div:nth-of-type(1) {
  display: none;
}
.carousel-two-wrapper .dis-two div:nth-of-type(2) {
  display: block;
}
.carousel-two-wrapper .text1 {
  display: none;
}
.carousel-two-wrapper .text2 {
  margin-top: -0.3em;
  display: block;
}

